.game-board {
  --size: 30vh;
  height: var(--size);
  width: var(--size);
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  background-color: #4A148C;
  border-radius: 3px;
}

.board-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
}


button:focus {
  outline: none;
}

.game {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.status {
  margin-bottom: 10px;
}

.options-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.option-button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  --size: 25px;
  height: var(--size);
  width: var(--size);
  border: none;
  margin: 0 5px;
  border-radius: 3px;
  background-color: #263238;
  color: #fff;
  text-align: center;
  vertical-align: middle;
}

.option-button:hover {
  background-color: #546E7A
}
.option-button:focus {
  background-color: #546E7A
}

.last-move-button {
  border: none;
  padding: 2px 6px;
  margin-bottom: 3px;
  border-radius: 3px;
  background-color: #263238;
  color: #fff;
}

.last-move-button:hover {
  background-color: #546E7A
}
.last-move-button:focus {
  background-color: #546E7A
}

@-webkit-keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

@keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
